import * as actionTypes from './constants'

import {
  getTopBanners,
  getHotRecommends,
  getHotRecommends1,
  getNewAlbum,
  getTopList,
} from '@/services/recommend'
import { getArtistList } from '../../../../../services/recommend'

// banner 
const changeTopBannersAction = (res) => ({
  type: actionTypes.CHANGE_TOP_BANNERS,
  topBanners: res.banners
})

export const getTopBannersAction = () => {
  return (dispatch) => {
    getTopBanners().then(res => {
      dispatch(changeTopBannersAction(res));
    });
  }
}

const changeHotRecommends = (res) => ({
  type: actionTypes.CHANGE_HOT_RECOMMENDS,
  hotRecommends: res.result
})

// 拦截action，发起网络请求后，在dispatch(action)
export const getHotRecommendsAction = (limit) => {
  return dispatch => {
    getHotRecommends(limit).then(res => {
      res && res.code === 200 && res.result
        && dispatch(changeHotRecommends(res));

    })
  }
}
// 新碟上架
const changeNewAlbum = (res) => ({
  type: actionTypes.CHANGE_NEW_ALBUM,
  newAlbum: res.albums
})

export const getNewAlbumAction = (limit = 10) => {
  return (dispatch) => {
    getNewAlbum(limit).then(res => {
      // console.log('getAlubum==>', res);
      res && res.code === 200 && res.albums
        && dispatch(changeNewAlbum(res));
    }).catch(err => {
      console.error('getNewAlbumAction==>', err);
    });
  }
}
// 三个榜单数据
const changeTopUpListAction = (res) => ({
  type: actionTypes.CHANG_TOP_UP_LIST,
  topUpList: res.playlist
})
const changeTopNewListAction = (res) => ({
  type: actionTypes.CHANG_TOP_NEW_LIST,
  topNewList: res.playlist
})
const changeTopOriginListAction = (res) => ({
  type: actionTypes.CHANG_TOP_ORIGIN_LIST,
  topOriginList: res.playlist
})
// 通过redux-thunk拦截action请求，拦截后发起网络请求
// 网络请求响应后，在放行action
export const getTopData = (idx = 0) => {
  return dispatch => {
    // 根据不同的idx，修改不同的数据
    getTopList(idx).then(res => {
      switch (idx) {
        case 0:
          dispatch(changeTopNewListAction(res));
          break;
        case 2:
          dispatch(changeTopOriginListAction(res));
          break;
        case 3:
          dispatch(changeTopUpListAction(res));
          break;
        default:
          console.log("其他数据处理");
      }
    })
  }
}

// getHotRecommends1

const changeHotRecommends1 = (result) => ({
  type: actionTypes.CHANGE_HOT_RECOMMENDS1,
  hotRecommends1: result
})

export const getHotRecommends1Action = function (limit) {
  // 这里的action返回的是一个函数，
  return (dispatch) => {
    //将来thunk内部，会调用这个返回的函数
    // 我们需要在这里书写我们的异步处理代码
    // 处理成功之后，调用真正的dispatch，派发事件
    getHotRecommends1(limit).then(res => {
      res && res.code === 200 && res.result
        && dispatch(changeHotRecommends1(res.result));
    }).catch((err) => console.error(err));
  }
}

// 右侧入住歌手
const changeSettleSingersAction = (res) => ({
  type: actionTypes.CHANGE_SETTLE_SINGERS,
  settleSingers: res
})

export const getSettleSingersAction = (limit = 5, cat = 5001) => {
  return (dispatch) => {
    getArtistList(limit, cat).then(res => {
      res && res.code === 200 && res.artists
        && dispatch(changeSettleSingersAction(res.artists));
    }).catch(err => {
      console.error('getArtistListAction==>', err);
    });
  }
}