import styled from 'styled-components'

export const HeaderWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  height: 35px;
  border-bottom: 2px solid #c10d0c;
  .left{
    display: flex;
    align-items: center;
    .title{
      display: flex;
      align-items: center;
      font-size: 20px;
      height: 35px;
      margin-right: 20px;
      line-height: 28px;
      i{
        display: inline-block;
        width: 30px;
        height: 35px;
      background-position: -225px -152px;;
        
      }
    }
    .links{
      color:#666666
    }
    
    .line{
      color:#cccccc;
      padding: 0 12px;
    }
    span.line:last-child{
      display: none;
    }
  }
  .right{
    line-height: 35px;
    .more{
      display: flex;
      align-items: center;
      font-size: 12px;
      color:#666666;
      i{
        display: inline-block;
        width: 12px;
        height: 12px;
        margin-left: 4px;
        background-position: 0 -240px;;
      }
    }
  }

`