import React, { memo, useCallback, useEffect, useRef, useState } from 'react'
import { useDispatch, useSelector, shallowEqual } from 'react-redux'

import { getTopBannersAction } from '../../store/actionCreators'
import { Carousel } from 'antd';
import {
  BannerControl,
  BannerLeft,
  BannerRight,
  BannerWrapper
} from './style'

export default memo(function YTopBanner() {
  // 1.内部 state
  const [currentIndex, setCurrentIndex] = useState(0);

  //2.redux相关的 组件与redux关联，获取数据和进行dispatch操作
  const dispatch = useDispatch();

  const { topBanners } = useSelector(state => ({
    // topBanners: state.get('recommend').get('topBanners')
    topBanners: state.getIn(['recommend', 'topBanners'])
  }), shallowEqual);

  // 3.其他的hooks
  const bannerRef = useRef();

  // 在该组件挂在好之后，发起异步请求，当依赖改变的时候也会调用
  useEffect(() => {
    dispatch(getTopBannersAction());
  }, [dispatch]);

  // 将函数传递给子组件使用时，使用useCallback进行包裹，优化性能
  // 有记忆的函数，在依赖不发生改变的情况下，返回的是同一个函数
  const bannerChange = useCallback((from, to) => {
    setCurrentIndex(to);
  }, []);

  // 4.其他业务逻辑
  const bgImg = topBanners[currentIndex]?.imageUrl + '?imageView&blur=40x20';
  return (
    <BannerWrapper bgImg={bgImg} >
      <div className="content wrap-v2">
        {/* 左侧的轮播图 */}
        <BannerLeft>
          <Carousel effect="fade" autoplay
            beforeChange={bannerChange}
            ref={bannerRef}>
            {
              topBanners.map(item => {
                return (
                  <div className='banner-item' key={item.imageUrl}>
                    <a href={item.url} target="_blank" className='item-link'>
                      <img src={item.imageUrl} alt={item.typeTitle} className='banner-img' />
                    </a>
                  </div>
                )
              })
            }
          </Carousel>
        </BannerLeft>
        {/* 右侧的下载App */}
        <BannerRight>
          <a href="https://music.163.com/#/download" target="_blank" className='download'></a>
        </BannerRight>
        {/* 左右控制按钮 */}
        <BannerControl>
          <button className='btn left' onClick={e => { bannerRef.current.prev() }}></button>
          <button className='btn right' onClick={e => { bannerRef.current.next() }}></button>
        </BannerControl>
      </div>
    </BannerWrapper >
  )
})
