import React, { memo, useEffect } from 'react'
import { shallowEqual, useDispatch, useSelector } from 'react-redux';
import YThemeHeaderSmall from '../../../../../../components/theme-header-small';
import { getSettleSingersAction } from '../../store/actionCreators';

import {
  SettleSingerWrapper
} from './style'

export default memo(function YSettleSinger() {
  // state

  // redux -hooks
  const dispatch = useDispatch();
  const { settleSingers } = useSelector((state) => ({
    settleSingers: state.getIn(['recommend', 'settleSingers'])
  }), shallowEqual);
  // other hooks
  useEffect(() => {
    dispatch(getSettleSingersAction());
  }, [dispatch]);

  // func code


  return (
    <SettleSingerWrapper>
      <YThemeHeaderSmall title="入驻歌手" more="查看全部>" />
      {
        settleSingers.map(item => {
          return (
            <a href='/' className='item-singer' key={item.id}>
              <img className='item-img' src={item.img1v1Url} alt="" />
              <div className="item-info">
                <div className='item-alias'>{item?.alias[0] || item.name}</div>
                <div className='item-name'>{item.name}</div>
              </div>
            </a>
          )
        })
      }
      <div className="apply-for">
        <a href="/">申请成为网易音乐人</a>
      </div>
    </SettleSingerWrapper>
  )
})
