// 导入第三方库
import React, { memo, useEffect } from 'react'
import { shallowEqual, useDispatch, useSelector } from 'react-redux'

// 导入其他工具类
import { HOT_RECOMMEND_LIMIT } from '@/common/constant'
import { getHotRecommends1Action } from '../../store/actionCreators'

// 导入组件
import YThemeHeaderRCM from '@/components/theme-header-rcm'
import {
  HotRecommendWrapper
} from './style'
import YThemeCover from '@/components/theme-cover'

export default memo(function () {
  // state

  // redux hooks
  const dispatch = useDispatch();

  // 这里通过useSelector获取的数据，会和上次获取的数据进行比较
  // 默认使用的是引用比较，如果新旧数据引用一样的话，不会重新渲染组件。
  // useSelector在使用的时候，要求传入的回调函数，该函数会在内部执行，
  // 并且以该回调函数的返回值，作为useSelector的返回值使用，函数返回对象{}，都是不同的引用

  // 下面的代码改成了使用浅层比较，会比较新旧数据的key值，减少没有必要的渲染，更能优化性能
  const { hotRecommends1 = [] } = useSelector((state) => ({
    hotRecommends1: state.getIn(['recommend', 'hotRecommends1'])
  }), shallowEqual);


  //other
  // 在组件挂在完成之后，立马调用传入的回调函数，
  // useEffect的返回值会作为组件卸载时候的回调使用。
  // 这样 使用useEffect就模拟了，组件的主要生命周期
  useEffect(() => {
    dispatch(getHotRecommends1Action(HOT_RECOMMEND_LIMIT));
  }, [dispatch]);

  // func code
  return (
    <HotRecommendWrapper>
      <YThemeHeaderRCM title='热门推荐' links={['华语', '流行', '摇滚', '民谣', '电子']} />
      <div className="recommend-list">
        {
          hotRecommends1.map(item => {
            return (
              <YThemeCover info={item} key={item.id} />
            )
          })
        }
      </div>
    </HotRecommendWrapper>
  )
});



