import styled from 'styled-components'

export const HotRadioWrapper = styled.div`
    padding: 20px;
    overflow: hidden;
    .item-radio{
      display: flex;
      text-decoration: none;
      &:hover{
        background-color: #f4f4f4;
      }
      margin-top: 14px;
      .item-img{
        width: 40px;
        height: 40px;
      }
      .item-info{
        width: 160px;
        margin-left: 8px;
        .item-name{
          font-weight: 600;
          font-size: 12px;
          color: #333;
        }
        .item-position{
          font-size: 12px;
          margin-top: 6px;
          color: #666;
        }
      }
    }
`;