import styled from 'styled-components'
export const SettleSingerWrapper = styled.div`
    padding: 20px;

    .item-singer{
      display: flex;
      align-items: center;
      margin-top: 14px;
      margin-left: 4px;
      text-decoration: none;
      &:hover{
        background-color: #f4f4f4;
      }
      .item-img{
        width: 62px;
        height: 62px;
      }
      .item-info{
        margin: 10px 0 0 8px;
        .item-alias{
          color: #333;
          font-size: 14px;
          font-weight: 700;
        }
        .item-name{
          color: #666;
          margin-top: 5px;
        }
      }
    }

    .apply-for{
      margin-top: 12px;

      a{
        display: block;
        height: 31px;
        line-height: 31px;
        border:1px solid #c3c3c3;
        background-color: #fafafa;
        text-align: center;
        text-decoration: none;
        border-radius: 4px;
        color: #333;
        font-size:12px;
        font-weight: 700;;
      }
    }
`;