import React, { memo } from 'react'
import { HeaderSmallSwrapper } from './style';

export default memo(function YHeaderSmall(props) {
  const { title, more } = props;
  return (
    <HeaderSmallSwrapper>
      <span>{title}</span>
      <a href='/'>{more}</a>
    </HeaderSmallSwrapper>
  )
})
