import request from './request'

export function getTopBanners() {
  return request({
    url: '/banner'
  });
}

export function getHotRecommends(limit = 8) {
  return request({
    url: '/personalized',
    params: {
      limit
    }
  });
}

export function getHotRecommends1(limit1 = 8) {
  return request({
    url: 'personalized',
    params: {
      limit: limit1
    }
  })
}
// 获取新碟上架数据
export function getNewAlbum(limit) {
  return request({
    url: '/top/album',
    params: {
      limit
    }
  })
}

// 获取榜单数据 
export function getTopList(idx) {
  return request({
    url: "/top/list",
    params: {
      idx
    }
  })
}

export function getArtistList(limit, cat) {
  return request({
    url: '/artist/list',
    params: {
      limit,
      cat
    }
  });

}