// 第三方库
import React, { memo, useEffect, useRef } from 'react'
import { shallowEqual, useDispatch, useSelector } from 'react-redux'
// 功能函数
import { getNewAlbumAction } from '../../store/actionCreators'
import { NEW_ALBUM_LIMIT } from '@/common/constant'
// 组件相关
import YThemeHeaderRCM from '@/components/theme-header-rcm'
import YAlbumCover from "@/components/album-cover";
import { Carousel } from 'antd';
import {
  NewAlbumWrapper
} from './style'

export default memo(function YNewAlbum() {
  // state

  // redux hooks
  const dispatch = useDispatch();

  const state = useSelector(state => ({
    newAlbum: state.getIn(['recommend', 'newAlbum']),
  }), shallowEqual);

  // 其他hooks
  const carouselRef = useRef();
  useEffect(() => {
    dispatch(getNewAlbumAction(NEW_ALBUM_LIMIT));
  }, [dispatch]);

  // 功能代码

  return (
    <NewAlbumWrapper>
      <YThemeHeaderRCM title='新碟上架' />
      <div className="content">
        <div className="arrow arrow-left sprite_02"
          onClick={e => carouselRef.current.prev()}>
        </div>
        <div className="album">
          <Carousel ref={carouselRef} dots={false}>
            {
              [0, 1].map(item => {
                return (
                  <div key={item} className="page">
                    {
                      state.newAlbum.slice(item * 5, (item + 1) * 5).map(item => {
                        return (
                          <YAlbumCover key={item.id} info={item} />
                        )
                      })
                    }
                  </div>
                )
              })
            }
          </Carousel>
        </div>
        <div className="arrow arrow-right sprite_02"
          onClick={e => carouselRef.current.next()}>
        </div>
      </div>
    </NewAlbumWrapper>
  )
})
