import styled from 'styled-components'

export const UserLoginWrapper = styled.div`
  display: flex;
  align-self: center;
  flex-direction: column;
  height: 126px;
  padding: 16px 22px;
  background-position: 0 0;
  p.title{
    line-height: 25px;
  }
  a.link{
    width: 100px;
    color: #fff;
    text-align: center;
    text-decoration: none;
    font-size: 12px;
    height: 31px;
    display: inline-block;
    line-height: 31px;
    background-position: 0 -195px;
    margin-top: 10px;
    align-self: center;
  }

`;