import React, { Fragment, memo } from 'react'
import PropTypes from 'prop-types'
import { HeaderWrapper } from './style'

const YThemeHeaderRCM = memo(function (props) {
  const { title, links = [] } = props;
  return (
    <HeaderWrapper>
      <div className="left">
        <h2 className='title'>
          <i className='sprite_02'></i>
          {title}
        </h2>
        <div className='links'>
          {
            links.map(item => {
              return (
                <Fragment key={item}>
                  <a href="#/" className='link-item'>{item}</a>
                  <span className='line'>|</span>
                </Fragment>
              )
            })
          }

        </div>
      </div>
      <div className="right">
        <a href="#/" className='more'>更多
          <i className='sprite_02'></i>
        </a>
      </div>
    </HeaderWrapper>
  )
})
YThemeHeaderRCM.propTypes = {
  title: PropTypes.string.isRequired,
  links: PropTypes.array
}
YThemeHeaderRCM.defaultProps = {
  link: []
}

export default YThemeHeaderRCM;
