import React, { memo } from 'react'
import { HotRadioWrapper } from './style'
import { getSizeImage } from '@/utils/format-utils';

import {
  hotRadios
} from '@/services/local-data'
import THhemeHeaderSmall from '../../../../../../components/theme-header-small'

export default memo(function YHotRadio() {
  return (
    <HotRadioWrapper>
      <THhemeHeaderSmall title='热门主播' />
      {
        hotRadios.map(item => {
          return (
            <a className='item-radio' key={item.url} href='/'>
              <img className='item-img' src={getSizeImage(item.picUrl, 40)} alt="" />
              <div className="item-info">
                <div className='item-name'>{item.name}</div>
                <div className='item-position text-nowrap'>{item.position}</div>
              </div>
            </a>
          )
        })
      }
    </HotRadioWrapper>
  )
})
