// 导入第三方库
import React, { memo, useEffect } from 'react'
import { shallowEqual, useDispatch, useSelector } from 'react-redux'
// 工具函数
import { getTopData } from '../../store/actionCreators'
// 组件
import YThemeHeaderRCM from '@/components/theme-header-rcm'
import YTopRanking from '@/components/top-ranking'
import {
  RankingWrapper
} from './style'
export default memo(function YRanking() {
  // state

  // redux-hooks
  const dispatch = useDispatch();
  // 下面的网络请求成功之后，这里的useSelector会被重新执行，获取最新的数据
  const state = useSelector(state => {
    // console.log('store-state', state.toJS());
    return {
      topUpList: state.getIn(['recommend', 'topUpList']),
      topNewList: state.getIn(['recommend', 'topNewList']),
      topOriginList: state.getIn(['recommend', 'topOriginList']),
    }
  }, shallowEqual);

  // hooks
  useEffect(() => {
    dispatch(getTopData(0));
    dispatch(getTopData(2));
    dispatch(getTopData(3));
  }, [dispatch]);
  //其他逻辑代码

  return (
    <RankingWrapper>
      <YThemeHeaderRCM title='榜单' />
      <div className='tops'>
        <YTopRanking info={state.topUpList} />
        <YTopRanking info={state.topNewList} />
        <YTopRanking info={state.topOriginList} />
      </div>
    </RankingWrapper>
  )
})
