import React, { memo } from 'react'

import {
  RecommendWrapper,
  RecommendLeft,
  RecommendRight
} from './style'

import YTopBanner from './c-cpms/top-banner'
import YHotRecommend from './c-cpms/hot-recommend'
import YNewAlbum from './c-cpms/new-album'
import YRecommendRanking from './c-cpms/recommend-ranking'
import YUserLogin from './c-cpms/user-login'
import YSettleSinger from './c-cpms/settle-singer'
import YHotRadio from './c-cpms/hot-radio'
function YRecommend() {

  return (
    <RecommendWrapper>
      <YTopBanner />
      <div className="content wrap-v2">
        <RecommendLeft>
          <YHotRecommend />
          <YNewAlbum />
          <YRecommendRanking />
        </RecommendLeft>
        <RecommendRight>
          <YUserLogin />
          <YSettleSinger />
          <YHotRadio />
        </RecommendRight>
      </div>

    </RecommendWrapper>
  )
}
export default memo(YRecommend);
